.list {
    width: 60%;
    background-color: #FFF;
    margin-top: 0;
    padding-left: 0;

    &__item {
        list-style: none;
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-auto-rows: minmax(70px, auto);
        background-color: #fff;
        text-align: center;
        align-items: center;
        font-weight: bold;
        color: rgb(117, 117, 117);
        letter-spacing: 1px;

        &:hover {
            box-shadow: 0px 3px 12px 0px rgba(0,0,0,1);
        }

        &:nth-child(odd) {
            background-color: rgb(201, 231, 245);
        }

        &-terminal {
            display: flex;
            justify-content: center;

            div {
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                width: 40px;
                height: 40px;
                font-size: 25px;
            }

            .terminal-a {
                border: 2px solid rgb(10, 211, 10);
                color:rgb(10, 211, 10);
            }

            .terminal-d {
                border: 2px solid rgb(35, 204, 255);
                color:rgb(35, 204, 255);
            }
        }

        &-airline {
            display: flex;
            align-items: center;

            img {
                margin-right: 10px;
            }
        }

        img {
            width: 60px;
        }
    }
}